
.toggle-enter {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.toggle-enter-active, .toggle-leave-active {
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.toggle-enter-to, .toggle-leave {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.toggle-leave-to {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}



