
.process {
  padding: 40px 0;
}
.process ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 14px;
    text-align: center;
}
.process li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    position: relative;
    padding-bottom: 25px;
    border-bottom: 2px solid #ccc;
    letter-spacing: 3px;
    color: #999;
}
.process li:after {
      content: '';
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 8px;
      background: #ccc;
      position: absolute;
      left: 50%;
      bottom: -8px;
      margin-left: -8px;
}
.process li.active {
      color: #d1434a;
      border-color: #d1434a;
}
.process li.active:after {
        background: #d1434a;
}



