
.address-item {
  margin-bottom: 30px;
}
.address-item .title {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 22px;
}
.address-item .list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.address-item .list li {
      width: 24%;
      height: 163px;
      border: 2px solid #ccc;
      padding: 20px;
      font-size: 14px;
      -webkit-transition: all 0.3s linear;
      transition: all 0.3s linear;
      cursor: pointer;
      margin: 0.5%;
}
.address-item .list li:hover {
        border-color: #ee7a23;
}
.address-item .list li.active {
        border-color: #ee7a23;
}
.address-item .list li.active .bottom .not-default {
          display: inline;
}
.address-item .list li.addAddress {
        text-align: center;
}
.address-item .list li.addAddress i {
          font-size: 50px;
          margin: 20px 0;
}
@media (max-width: 768px) {
.address-item .list li {
          width: 49%;
}
}
.address-item .list .name {
      font-size: 16px;
      padding-bottom: 10px;
}
.address-item .list .street {
      line-height: 20px;
      height: 40px;
      overflow: hidden;
}
.address-item .list .tel {
      line-height: 30px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
}
.address-item .list .bottom {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      margin-top: 10px;
}
.address-item .list .bottom .default {
        color: #ee7a23;
}
.address-item .list .bottom .not-default {
        display: none;
        -webkit-transition: all 0.3s linear;
        transition: all 0.3s linear;
}
.address-item .list .bottom .not-default:hover {
          color: #d1434a;
}
.address-item .list .bottom .delete i {
        font-size: 30px;
        margin-top: -10px;
        -webkit-transition: all 0.3s linear;
        transition: all 0.3s linear;
}
.address-item .list .bottom .delete i:hover {
          color: #ee7a23;
}
.address-item .list .price {
      font-size: 16px;
      margin-top: 10px;
      color: #ee7a23;
}
.address-item .list .description {
      margin-top: 20px;
      line-height: 1.3;
}
.address-item .more {
    text-align: center;
    line-height: 50px;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}
.address-item .more:hover {
      color: #ee7a23;
}



