
.header {
  height: 72px;
  background: #fff;
  overflow: hidden;
}
.header .logo {
    margin-top: -4px;
    margin-left: -18px;
}
.header .logo img {
      margin: 0px 0 0 13px;
}
.header .login-cart {
    margin-top: 20px;
    margin-right: 20px;
}
.header .login-cart .username i {
      font-size: 23px;
      vertical-align: -3px;
}
.header .login-cart .cart {
      position: relative;
}
.header .login-cart .cart i {
        font-size: 22px;
}
.header .login-cart .cart .cart-count {
        position: absolute;
        top: -10px;
        left: 16px;
        width: 22px;
        height: 22px;
        text-align: center;
        line-height: 22px;
        border-radius: 50%;
        color: #fff;
        background: rgba(209, 67, 74, 0.8);
}
.header .login-cart a {
      -webkit-transition: all 0.3s linear;
      transition: all 0.3s linear;
      margin-left: 10px;
}
.header .login-cart a:hover {
        color: #d1434a;
}
.header .md-top h2 {
    font-size: 18px;
    line-height: 40px;
    margin-bottom: 10px;
}
.header .md-content input {
    width: 100%;
    height: 43px;
    border: 1px solid #ddd;
    padding-left: 20px;
    font-size: 12px;
    margin-bottom: 20px;
}
.header .md-content .login-btn {
    height: 40px;
    line-height: 40px;
    color: #fff;
    text-align: center;
    background: #009de6;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.header .md-content .login-btn:hover {
      background: #61b1ef;
}



