
.sort-way {
  margin-top: 60px;
  height: 57px;
  line-height: 57px;
  text-align: right;
  background: #fff;
  padding: 0 20px;
}
.sort-way span {
    margin-right: 20px;
}
.sort-way span.default-tag {
      color: #ee7a23;
}
.sort-way .price {
    -webkit-transition: all .3s;
    transition: all .3s;
}
.sort-way .price:hover {
      color: #ee7a23;
}
.sort-way .price i {
      line-height: 1;
      vertical-align: -1px;
      margin-left: -5px;
}
.sort-way .price .reverse {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.sort-way .show-filter-btn {
    display: none;
}
@media (max-width: 768px) {
.sort-way {
      text-align: left;
      background: #f0f0f0;
      margin-top: 0;
}
.sort-way .sort-by {
        display: none;
}
.sort-way .show-filter-btn {
        display: block;
        float: right;
        font-size: 12px;
        letter-spacing: 3px;
}
}
.goods-display {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 30px;
}
.goods-display .filter-nav {
    width: 260px;
    padding: 0 20px;
}
.goods-display .filter-nav dt {
      font-weight: bold;
      margin-bottom: 32px;
}
.goods-display .filter-nav dd {
      height: 26px;
      line-height: 26px;
      margin-bottom: 20px;
      -webkit-transition: padding-left .3s;
      transition: padding-left .3s;
      cursor: pointer;
}
.goods-display .filter-nav dd:hover, .goods-display .filter-nav dd.active {
        border-left: 2px solid #ee7a23;
        padding-left: 12px;
}
@media (max-width: 768px) {
.goods-display .filter-nav {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
}
.goods-display .filter-nav dl {
          position: absolute;
          right: -233px;
          top: 0;
          width: 233px;
          height: 100%;
          background: #fff;
          -webkit-transition: all .5s;
          transition: all .5s;
}
.goods-display .filter-nav dl dt {
            height: 50px;
            line-height: 50px;
            padding: 0 18px;
            margin-bottom: 0;
}
.goods-display .filter-nav dl dd {
            height: 40px;
            line-height: 40px;
            margin-bottom: 0;
            border-bottom: 1px solid #ddd;
            padding-left: 18px;
}
.goods-display .filter-nav dl dd:hover, .goods-display .filter-nav dl dd.active {
              padding-left: 22px;
}
.goods-display .filter-nav.is-show {
          display: block;
}
}
.goods-display .goods-display-result {
    width: 100%;
}
.goods-display .goods-display-result li {
      width: 25%;
      padding-left: 18px;
      margin-bottom: 18px;
      float: left;
      -webkit-transition: all .7s;
      transition: all .7s;
}
.goods-display .goods-display-result li .content {
        border: 2px solid #eee;
        background: #fff;
        -webkit-transition: all .7s;
        transition: all .7s;
}
.goods-display .goods-display-result li .content:hover {
          border-color: #ee7a23;
          -webkit-box-shadow: 0 0 5px #ddd;
                  box-shadow: 0 0 5px #ddd;
          -webkit-transform: translateY(-5px);
                  transform: translateY(-5px);
}
.goods-display .goods-display-result li .text {
        padding: 20px 10px 10px;
}
.goods-display .goods-display-result li .img {
        padding-top: 100%;
        display: block;
        background-position: center center;
        background-size: cover;
}
.goods-display .goods-display-result li h3 {
        font-weight: bold;
        line-height: 20px;
        height: 40px;
        margin-bottom: 10px;
        overflow: hidden;
}
.goods-display .goods-display-result li .price {
        line-height: 30px;
        color: #d1434a;
        font-size: 18px;
}
.goods-display .goods-display-result li .add-cart {
        display: block;
        height: 40px;
        font-weight: bold;
        line-height: 40px;
        text-align: center;
        margin-top: 10px;
        border: 1px solid #d1434a;
        color: #d1434a;
        -webkit-transition: all .4s;
        transition: all .4s;
}
.goods-display .goods-display-result li .add-cart:hover {
          background: #ffe5e6;
}
@media (max-width: 992px) {
.goods-display .goods-display-result li {
          width: 33.33333%;
          padding: 0 6px;
          margin-bottom: 12px;
}
}
@media (max-width: 500px) {
.goods-display .goods-display-result li {
          width: 50%;
}
}
.loading {
  clear: both;
  text-align: center;
}
.md-add-cart-success .md-msg i {
  font-size: 22px;
  vertical-align: -3px;
  color: #a94442;
  margin-right: 7px;
}



