
.position {
  height: 45px;
  line-height: 45px;
  background: #f0f0f0;
}
.position a {
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}
.position a:first-child {
      margin-left: 11px;
}
.position a:hover {
      color: #d1434a;
}
.position span {
    color: #d1434a;
}
@media (max-width: 768px) {
.position {
      display: none;
}
}



