
.footer {
  height: 100px;
  line-height: 100px;
  color: #666;
  margin-top: 70px;
  font-size: 13px;
  text-align: right;
}
.footer .container {
    border-top: 1px solid #ddd;
}
.footer a, .footer span {
    margin-left: 35px;
}
@media (max-width: 768px) {
.footer {
      height: 35px;
      line-height: 35px;
      margin-top: 20px;
      text-align: center;
}
.footer .container {
        padding: 10px 0;
}
.footer a, .footer span {
        margin-left: 0;
        display: block;
}
}



